
export default {
  name: 'Login',
  data () {
    return {
      userName: '',
      pass: ''
    }
  },
  computed: {

  },
  methods: {
    login: function (e) {
      if (this.userName === 'anjana' && this.pass === '1234') {
        this.$store.commit('setUserName', this.userName)
        this.$localStorage.set('userName', this.userName)
        this.$localStorage.set('userLoggedIn', true)
        this.$store.commit('checkUser', true)
        this.$router.push('/book-list')
      }
    },
    openFbLoginDialog () {
      FB.login(this.checkLoginState, { scope: 'email' })
    },
    checkLoginState: function (response) {
      var self = this;
      if (response.status === 'connected') {
        FB.api('/me', { fields: 'name,email' }, function(profile) {
          self.$store.commit('setUserName', profile.name)
          self.$localStorage.set('userName', profile.name)
          self.$localStorage.set('userLoggedIn', true)
          self.$store.commit('checkUser', true)
          self.$router.push('/book-list')

        });
      } else if (response.status === 'not_authorized') {
        // the user is logged in to Facebook,
        // but has not authenticated your app
      } else {
        // the user isn't logged in to Facebook.
      }
    }
  },
  components: {
  }
}



